
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
  },
  methods: {
    encode(str: string): string {
      console.log(str);
      return btoa(str || '');
    },
    decode(str: string): string {
      try {
        return atob(str || '');
      } catch (err) {
        return '';
      }
    },
  },
})
export default class Base64 extends Vue {
  message = '';

  async mounted() {
    this.message = await navigator.clipboard.readText();
  }
}
